import ReactDom from 'react-dom';
import '../../corecss/searchpopup.css';
import { useDispatch, useSelector } from 'react-redux';
import { currview } from '../../features/profile';
import { useNavigate } from 'react-router-dom';


const Searchpopup = ({ open, slice }) => {

    let disp = useDispatch();
    let nav = useNavigate();

    let theme = useSelector((state) => { return state.themeyr.value });

    const profviewer = (username) => {

        disp(currview(username));
        nav(`/profile/${username}`);

    }

    

    return ReactDom.createPortal(
        <>

            <div className='blurreonee'></div>
            <div className='blurretwoe'></div>
            <div className='blurrethreee'></div>
            <div className='blurrefoure'></div>
            <div className={theme ? 'searchmodal' : 'searchmodal searchmodalnight'}>

                <ul className="searchusers">

                    {

                        slice.length == 0

                            ?

                            <>
                                <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class={theme ? "bi bi-person-x nosearchemoji" : "bi bi-person-x nosearchemoji nighttextsearch "} viewBox="0 0 16 16">
                                    <path d="M11 5a3 3 0 1 1-6 0 3 3 0 0 1 6 0M8 7a2 2 0 1 0 0-4 2 2 0 0 0 0 4m.256 7a4.5 4.5 0 0 1-.229-1.004H3c.001-.246.154-.986.832-1.664C4.484 10.68 5.711 10 8 10q.39 0 .74.025c.226-.341.496-.65.804-.918Q8.844 9.002 8 9c-5 0-6 3-6 4s1 1 1 1z" />
                                    <path d="M12.5 16a3.5 3.5 0 1 0 0-7 3.5 3.5 0 0 0 0 7m-.646-4.854.646.647.646-.647a.5.5 0 0 1 .708.708l-.647.646.647.646a.5.5 0 0 1-.708.708l-.646-.647-.646.647a.5.5 0 0 1-.708-.708l.647-.646-.647-.646a.5.5 0 0 1 .708-.708" />
                                </svg>
                                <span className={theme ? "noresults" : "noresults nighttextsearch"}>No Users Found..</span>
                            </>


                            :

                            slice.map((each) => (
                                <li className={theme ? "searchlistitem" : "searchlistitem searchlistitemnight"} onClick={() => { profviewer(each.usr) }} key={each.usr}>
                                    <div className="listdive">
                                        <span className={theme ? "listusernameshortee" : "listusernameshortee nighttextsearch"}>{each.usr}</span>
                                        <span className={theme ? "listfnameshortee" : "listfnameshortee nighttextsearch"}>{each.fname}</span>
                                        <img className="listpfpee" src={each.pfp}></img>
                                    </div>
                                </li>
                            ))
                    }

                </ul>

            </div>
        </>

        , document.getElementById('portal')
    );
}

export default Searchpopup;